<template>
<div class="wrapper" ref="wrapper">
  <div class="content">
    <!--  -->
    <div v-if="pulldown" class="pulldown">
      <div class="clear" v-if="pullDownTip.showLoding">
        <!-- <div class="clear-left"><img width="30" src="assets/img/loading.svg"></div> -->
        <div class="clear-right">{{pullDownTip.text}}</div>
      </div>
    </div>
    <!--  -->
    <slot></slot>
    <!--  -->
    <div v-if="pullup" class="pullup">
      <div class="clear" v-if="!isDone">
        <div class="clear-left"><loading-svg/></div>
        <div class="clear-right">.....</div>
      </div>
      <div class="list-donetip" v-if="!isLoading && isDone">
          <slot name="doneTip"></slot>
      </div>
    </div>
  </div>
</div>
</template>

<script>
import BScroll from 'better-scroll'
import LoadingSvg from '@/svg/loading'

export default {
  name: 'scroll',
  data () {
    return {
      bscroll: null,
      pullDownTip: {
        text: '',
        // translate: -50,
        showLoding: false
      },
      isLoading: false,
      isDone: false
    }
  },
  components: {
    LoadingSvg
  },
  props: {
    // 
    pullup: {
      type: Boolean,
      default: false
    },
    // 
    pulldown: {
      type: Boolean,
      default: false
    }
  },
  mounted () {
    // better-scroll
    this.bscroll = new BScroll(this.$refs.wrapper, { click: true, pullUpLoad: true, pullDownRefresh: true, probeType: 3 })
    this.initScroll()
  },
  methods: {
    // refresh
    scrollRefresh () {
      this.bscroll && this.bscroll.refresh()
    },
    // scrollTo
    scrollTo (x, y, time = 500) {
      this.bscroll && this.bscroll.scrollTo(x, y, time)
    },
    initScroll () {
      // 
      if (this.pullup) {
        this.bscroll.on('scrollEnd', () => {
          if (this.bscroll.y <= (this.bscroll.maxScrollY + 50)) {
            // 
            this.$bus.$on('infinitescroll.loadedDone', () => {
              this.isLoading = false
              this.isDone = true
            })
            // 
            this.$bus.$on('infinitescroll.finishLoad', (ret) => {
              this.isLoading = false
            })
            // 
            this.$bus.$on('infinitescroll.reInit', () => {
              this.isLoading = false
              this.isDone = false
            })
            this.$emit('scrollToEnd')
          }
        })
      }
      // 
      if (this.pulldown) {
        this.bscroll.on('scroll', (pos) => {
          // loding
          this.pullDownTip.showLoding = true
          // loding
          this.isLoading = false
          if (pos.y <= 50) {
            this.pullDownTip.text = ''
          }
        })
        this.bscroll.on('touchEnd', (pos) => {
          if (pos.y > 100) {
            // this.pullDownTip.translate = 0
            this.pullDownTip.text = ''
            // 
            this.$on('pullrefresh.finishLoad', this.resetParams)
            this.$emit('pulldown', pos)
          }
        })
      }
    },
    resetParams () {
      setTimeout(() => {
        this.isLoading = false
        this.isDone = false
        this.dragTip = {
          text: '',
          // translate: -50,
          showLoding: false
        }
      }, 1000)
    }
  }
}
</script>

<style scoped>
.wrapper{
  width: 100%;
  /*  absolute top bottom  */
  height: calc(100vh - 2.2rem);
  position: absolute;
  left: 0;
  top: 0;
  overflow: hidden;
}
.pulldown{
  height: 2.2rem;
}
.clear{
  width: 100%;
  height: 2.5rem;
  font-size: .7rem;
  display: flex;
  align-items: center;
  justify-content: center;
}
.clear-left{
  display: flex;
  align-items: center;
  margin-right: .25rem;
}
.list-donetip{
  font-size: .7rem;
  height: 2.5rem;
  line-height: 2.5rem;
  text-align: center;
}
</style>
